<template>
  <data-loader ref="loader" :handler="load">
    <template v-slot:default="{ busy }">
      <validation-observer ref="observer" v-slot="{ handleSubmit }">
        <form novalidate @submit.prevent="handleSubmit(submit)">
          <validation-provider v-slot="{ errors }" name="Username" rules="required|min:3|max:32">
            <v-text-field
              autofocus
              v-model="form.u"
              autocomplete="username"
              :error-messages="errors"
              label="Username"
              required
            ></v-text-field>
          </validation-provider>

          <validation-provider v-slot="{ errors }" name="Password" rules="required|strong_password">
            <v-text-field
              v-model="form.p"
              type="password"
              autocomplete="password"
              :error-messages="errors"
              label="Password"
              required
            ></v-text-field>
          </validation-provider>

          <div class="text-center">
          <v-btn color="secondary" large class="mt-6 px-16" :style="$vuetify.breakpoint.xs?'width:100%':''" type="submit" :loading="busy">Sign In</v-btn>
          </div>
        </form>
      </validation-observer>
    </template>
  </data-loader>
</template>

<script>

import { ValidationObserver } from 'vee-validate'

export default {
  name: 'DataForm',

  components: {
    ValidationObserver
  },

  data: () => ({
    form: {
      u: '',
      p: ''
    }
  }),

  methods: {
    load (isSubmit) {
      if (isSubmit) {
        return this.$store.dispatch('auth/signIn', this.form)
      }
    },
    async submit () {
      this.$refs.loader.load(true)
    }
  }
}

</script>

